import React from 'react';
import './StartButton.css';
import Button from '@mui/material/Button';
import { useState } from 'react';


const transcribeText = document.getElementById("test");

const StartButton = ({ onClick, shape, children, styles }) => {

  return (
    <div className='app__ButtonWrapper'>
      <Button variant={shape} size='large' onClick={onClick} sx={styles}>{children}</Button>
    </div>
  )
}

export default StartButton
