import React from 'react'
import { useState, useEffect } from 'react'
import { Avatar, Grid, Typography, Stack, Box, Card, CardActions, CardContent  } from '@mui/material';
import { animated, useSpring } from '@react-spring/web';
import { StartButton } from './../../components';
import ReactLoading from "react-loading";
import { deepOrange } from '@mui/material/colors';
import axios from 'axios';
// import { makeStyles } from "@mui/material/styles";


// const useStyles = makeStyles({
//   gridContainer: {
//     height: 'auto',
//   },

//   gridItem: {
//     display: "flex",
//     height: "100%",
//     minHeight: "100%",
//   }
// });

const TranscriptBlock = ({ transcript, speaker }) => {
  const isRecording = false;
  const [expanded, setExpanded] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [analysisResult, setAnalysisResult] = useState('');

  const handleClick =  async ( name ) => {
    setIsLoading(prevState => {
      let newState = true;
      if (prevState == false){
        newState = true;
      }
      return newState;
  })
  
    if (name === 'Wiki') {
      try {
        const response = await axios.post('http://127.0.0.1:8000/wiki-interaction/', {
          transcript: transcript,
        });
        setAnalysisResult(prevState => {
          const newState = JSON.stringify(response.data)
          return newState;
        });
        setIsLoading(prevState => {
          let newState = false;
          if (prevState == true){
            newState = false;
          }
          return newState;
        })
      } catch (error) {
        console.error(error);
      }
    }
    else if (name === 'ChatGPT') {
      try {
        const response = await axios.post('http://127.0.0.1:8000/gpt-interaction/', {
          transcript: transcript,
        });
        setAnalysisResult(prevState => {
          const newState = JSON.stringify(response.data)
          return newState;
        });
        setIsLoading(prevState => {
          let newState = false;
          if (prevState == true){
            newState = false;
          }
          return newState;
        })
      } catch (error) {
        console.error(error);
      }
    }

    else if (name === 'Google') {
      console.log("google");
    }
  }

  const leftSpring = useSpring({
    from: { transform: 'translateX(-100%)' },
    to: { transform: expanded ? 'translateX(-5%)' : 'translateX(0%)' },
    config: { duration: 500 }
  });

  return(

    <div>
      <animated.div style={{ display: 'flex', justifyContent: 'center', margin: '20px auto', borderRadius: '15px', ...leftSpring}}>
        <Box width='35%' sx={{transform: 'translateX(-7.5%)'}}>
          <Card>
            <CardContent>
            <Avatar
              alt={speaker}
            />
            <Typography variant="h5" component="h2">
              Speaker {speaker}
            </Typography>

            <Typography variant='body2' color='textSecondary'>
              {transcript}
            </Typography>
            </CardContent>
          </Card>
        </Box>
        <Box width='35%' sx={{transform: 'translateX(7.5%)'}}>
          <Card>
            <CardContent>
              {(isLoading) ? <ReactLoading type="spinningBubbles" color="rgba(116, 117, 117, 0.432)"/> : (analysisResult) ? <Typography variant='body2' color='textSecondary'> {analysisResult}
              </Typography> : <Typography variant='body2' color='textSecondary'> {transcript}
              </Typography>}
            </CardContent>
            <CardActions>
              <StartButton children='ChatGPT' onClick={()=>handleClick('ChatGPT')} styles={{ fontSize: 8, width: 30, height: 20 }}/>
              <StartButton children='Google' onClick={()=>handleClick('Google')} styles={{ fontSize: 8, width: 30, height: 20 }}/>
              <StartButton children='WikiPedia' onClick={()=>handleClick('Wiki')} styles={{ fontSize: 8, width: 30, height: 20 }}/>
            </CardActions>
          </Card>
        </Box>
      </animated.div>
    </div>
  )
}


export default TranscriptBlock
