
import './App.css';
import { Header, StartButton, TranscriptBlock } from './components';
import { useState, useRef, useEffect } from 'react';
import app from './firebase';
import { Route } from 'react-router-dom';
import logowhite from './assets/logowhite.png';

var mediaRecorder;


const App = () => {
  const [user, setUser] = useState(null);

  

  let logs;
  let prevSpeaker = '';
  const [currentButtonVariant, setCurrentButtonVariant] = useState('outlined');
  const [currentContent, setCurrentContent] = useState('');
  const [transciptLog, setTranscriptLog] = useState([]);
  const [record, setRecord] = useState(false);


  const startRecording = () => {
    setRecord((prevState) => {
      let newState = true;
      if (prevState === false){
        newState = true;
      }

      return newState;
    });
  };

  const stopRecording = async() => {
    if (record && mediaRecorder.state !== 'inactive') {
      mediaRecorder.stop();
      // mediaRecorder.stream.stop();
      // mediaRecorder.stream.getTracks().filter((i) => i.stop());
      mediaRecorder = null;
    }
    setRecord((prevState) => {
      let newState = false;
      if (prevState === true){
        newState = false;
      }

      return newState;
    });
  }


  const updateState = ( text ) => {
    setTranscriptLog(prevState => {
      const newState = prevState.map((obj, i) => {
        if (i === prevState.length-1) {
          return {...obj, transcript: obj.transcript + " " +  text};
        }
        return obj;
      });
  
      return newState;
    });
  };

  const audioToText = () => {
    console.log('start!!!!!!!!!')
    navigator.mediaDevices.getUserMedia({audio:true}).then((stream)=>{
      if (!MediaRecorder.isTypeSupported('audio/webm')) {
        return alert('Browser not supported!');
      }

      mediaRecorder = new MediaRecorder(stream, {
        mimeType: 'audio/webm',
      })

      const socket = new WebSocket('ws://Localhost:8000/listen');
      // const socket = new WebSocket(`wss://api.deepgram.com/v1/listen`, [
      //   'token',
      //   '45640f5b14890d99a442874df4c447b55957d6db',
      // ]);

      socket.onopen = () => {
        mediaRecorder.addEventListener('dataavailable', async (event) => {
          if (event.data.size > 0 && socket.readyState == 1) {
            socket.send(event.data);
          }
        });
        mediaRecorder.start(1000);
        console.log('started');
      }

      socket.onmessage = async (message) => {
        const received = JSON.parse(message.data);
        const text = received.channel.alternatives[0].transcript;
        const speaker = received.channel.alternatives[0].words[0].speaker;
        console.log(prevSpeaker);
        console.log(received);
        // key_1 : transcript_wiki
        // key_2 : transcript_chat
        // const speaker = received.channel.alternatives[0].words[0].speaker;
        // const speaker = 'me'
        if (text && received.speech_final) {
          console.log(transciptLog)

          if (speaker !== prevSpeaker){
            console.log('Add');
            setTranscriptLog(transciptLog => [...transciptLog, {transcript: text, speaker:speaker}]);

            prevSpeaker = speaker;
          }
          else{
            console.log('Edit');
            updateState(text);

          }
          
          
        }
      };

    })
  }

  useEffect(() => {
    if (record) {
      audioToText();
    }
  }, [record]);

  useEffect(() => {
    console.log(transciptLog);
  }, [transciptLog]);
  const handletranscribeChange = ( text ) => {
    setCurrentContent(currentContent + text);
  }

  const handleButtonChange = () => {
    if (currentButtonVariant === 'outlined') {
      setCurrentButtonVariant('contained');
      startRecording();

    }
    else {
      setCurrentButtonVariant('outlined');
      stopRecording();
    }
  }
  return (
    <div className="App">

      <Header/>
      <StartButton onClick={handleButtonChange} shape={currentButtonVariant} styles={{m:5, fontSize:24, width:160}}>Start</StartButton>
      <div className='app__transcript-container'>
        {transciptLog.map(( singleTranscript, index )=>
            <div key={index}>
              <TranscriptBlock transcript={singleTranscript.transcript} speaker={singleTranscript.speaker.toString()}/>
            </div>    
        )}
      {/* <TranscriptBlock transcript="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat"/> */}
      </div>
    </div>
  );
      }

export default App;
